@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Teko&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 img {
  border-radius:50%;
  width:50px;
  height:50px;
}
ul.topnav {
	width: 100%;
	height: 55px;
	overflow: hidden;
	box-shadow: 0 0 5px gray;
	margin: 0;
	padding: 0;
	list-style-type: none;
	background-color: #ffffff;
	position: fixed;
	top: 0px;
}
ul.topnav li {
	float: left;
}
ul.topnav li a {
	display: block;
	padding: 17.5px 16px;
	text-align: center;
	text-decoration: none;
	color: #130068;
}
ul.topnav li a:hover:not(.active) {
	background-color: #a9bce200;
}
ul.topnav li a.active {
	background-color: #da3c41;
}
ul.topnav li.right {
	float: right;
}
@media screen and (max-width: 480px) {
	ul.topnav li.right, ul.topnav li {
		float: none;
	}
}

ul.topnav li.center
{

	width: 100px;
	top: 50%;
	left: 50%;
	transform: translateY(-50%) translateX(-50%);
	padding: 0;
	position:absolute;
}
ul.topnav li.center a:hover:not(.active) 
{
	background-color: #ffffff00;
}
ul.topnav li.home
{
	width: 100px;
	top: 50%;
	left: 0%;
	transform: translateY(-50%) translateX(0%);
	padding: 0;
	position:absolute;
}

ul.topnav li font.home
{
	font-family: 'Teko', sans-serif;
	color: #130068;
	text-align: center;
	font-size:25px;
}
.worksContentArea {
    margin: auto;
    padding: 2rem;
    max-width: 1500px;
}


.worksContentArea a{
    text-decoration: none;
    color: #200050;
}

.contentAreaBox {
    margin: auto;
    display: grid;
    grid-gap: 0 2%;
    gap: 0 2%;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.workContentCard {
    background-size: cover;
    min-width: 6em;
    position: relative;
}


.workContentThmbnailWrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
}
.workContentThmbnailWrapper:before {
    content: "";
    display: block;
    padding-top: 100%;
}

.workContentThmbnail {
    position: absolute;
    background-size: cover;
    background-position: center;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.workContentTitle {
    font-family: sans-serif;
    font-weight: bold;
    padding-bottom: 2em;
}

* {
  margin: 0px;
  padding: 0px;
}

div.page
{
  position:absolute;
  -ms-overflow-style: none;
  top: 55px;
  width: 100%;
  background-color: #ffffff;
}
::-webkit-scrollbar {
  display: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


div.list
{
  display: grid;
    grid-template-columns: 100px 100px 100px;
    grid-template-rows: 100px 100px 100px;
}

div.list img
{
  width: 100%;
  height: 100%;
}
